import React from 'react'
import Grid from '@material-ui/core/Grid'
import Card from '@material-ui/core/Card'
import CardHeader from '@material-ui/core/CardHeader'
import CardContent from '@material-ui/core/CardContent'
import Typography from '@material-ui/core/Typography'
import green from '@material-ui/core/colors/green'

export const MCard = ({ children }) => (
    <Card raised style={ styles.cardStyle }>
        <Grid container>
            {children}
        </Grid>
    </Card>
)

export const MCardHeader = ({ children }) => {
    const { cardHeaderStyle, headerContentTyle } = styles
    return (
        <Grid item xs={12} sm={12} md={4} style={ cardHeaderStyle }>
            <Grid container alignItems="center" style={ headerContentTyle }>
                {children}
            </Grid>
        </Grid>
    )
}

export const Dates = ({ children }) => (
    <Typography variant="h6" align="center" style={ styles.headerTextStyle }>{children}</Typography>
)

export const Institution = ({ children }) => (
    <Typography variant="h5" align="center" style={ styles.headerTextStyle }>{children}</Typography>
)

export const Location = ({ children }) => (
    <Typography variant="h6" align="center" style={ styles.headerTextStyle }>{children}</Typography>
)

export const MCardContent = ({ title, children }) => (
    <Grid item xs={12} sm={12} md={8}>
        <CardHeader title={title} />
        <CardContent>
            <Grid container>
                {children}
            </Grid>
        </CardContent>
    </Grid>
)

export const Responsibility = ({ children }) => (
    <Typography style={ styles.responsibilityStyle } variant="body1" gutterBottom>{children}</Typography>
)

const styles = {
    cardStyle: {
        display: 'flex',
        margin: '20px'
    },
    cardHeaderStyle: {
        backgroundColor: green[500]
    },
    headerContentTyle: {
        width: '100%', 
        height: '100%'
    },
    headerTextStyle: {
        color: 'white',
        width: '100%',
        margin: '10px'
    },
    responsibilityStyle: {
        width: '100%',
        fontSize: 18
    }
}