import React from 'react'
import Grid from '@material-ui/core/Grid'
import Card from '@material-ui/core/Card'
import CardContent from '@material-ui/core/CardContent'
import Typography from '@material-ui/core/Typography'
import { Element } from 'react-scroll'

const ProfessionalSkills = ({ name, children }) => {
    const halfLength = Math.ceil(children.length / 2)
    const skills1 = children.slice(0, halfLength)
    const skills2 = children.slice(halfLength, children.length)
    return (
        <Element name={name}>
            <div style={{ marginTop: "100px" }}>
                <Typography align='center' variant="h4" gutterBottom>Professional Skills</Typography>
                <Card raised style={ styles.cardStyle }>
                    <Grid container>
                        {renderSkills(skills1)}
                        {renderSkills(skills2)}
                    </Grid>
                </Card>
            </div>
        </Element>
    )
}

const renderSkills = (skills) => (
    <Grid item xs={12} sm={12} md={6}>
        <CardContent>
            <Grid container>
                {skills}
            </Grid>
        </CardContent>
    </Grid>
)

const styles = {
    cardStyle: {
        display: 'flex',
        margin: '20px'
    }
}

export default ProfessionalSkills