import React from 'react'
import Grid from '@material-ui/core/Grid'
import Card from '@material-ui/core/Card'
import CardContent from '@material-ui/core/CardContent'
import Typography from '@material-ui/core/Typography'
import { Element } from 'react-scroll'

export const PersonalInformation = ({ name, children }) => (
    <Element name={name}>
        <Card raised style={ styles.cardStyle }>
            <Grid container>
                {children}
            </Grid>
        </Card>
    </Element>
)

export const About = ({ children }) => (
    <Grid item xs={12} sm={12} md={6}>
        <CardContent>
            <Typography variant="h4" gutterBottom>About</Typography>
            <Typography variant="body1" gutterBottom>
                {children}
            </Typography>
        </CardContent>
    </Grid>
)

export const BasicInformation = ({ children }) => (
    <Grid item xs={12} sm={12} md={6}>
        <CardContent>
            <Typography variant="h4" gutterBottom>Basic Information</Typography>
            <Grid container>
                {children}
            </Grid>
        </CardContent>
    </Grid>
)

export const PersonalData = ({ type, children }) => {
    const { nameStyle, valueStyle } = styles
    return (
        <Grid container>
            <Grid item xs={4}>
                <Typography style={ nameStyle } variant="body1" gutterBottom>{`${type}:`}</Typography>
            </Grid>
            <Grid item xs={8}>
                <Typography style={ valueStyle } variant="body1" gutterBottom>{children}</Typography>
            </Grid>
        </Grid>
    )
}

const styles = {
    cardStyle: {
        display: 'flex',
        margin: '20px'
    },
    nameStyle: {
        fontWeight: 'bold'
    },
    valueStyle: {
        wordWrap: 'break-word'
    }
}