import React from 'react'
import { ProgressBar } from './ProgressBar'
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'

export const Skill = ({ skill, percentage }) => {
    const { skillContainerStyle, textContainerStyle } = styles
    return (
        <Grid container style={ skillContainerStyle }>
            <Grid container justify="space-between" style={ textContainerStyle }>
                <Grid item>
                    <Typography variant="h6" gutterBottom>{skill}</Typography>
                </Grid>
                <Grid item>
                    <Typography variant="h6" gutterBottom>{`${percentage}%`}</Typography>
                </Grid>
            </Grid>
            <ProgressBar percentage={percentage} />
        </Grid>
    )
}

const styles = {
    skillContainerStyle: {
        margin: '10px'
    },
    textContainerStyle: {
        marginBottom: '10px'
    }
}