import React, { Component } from 'react'
import Typography from '@material-ui/core/Typography'
import ExpansionPanel from '@material-ui/core/ExpansionPanel'
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails'
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore'
import { Element } from 'react-scroll'
import ReactMarkdown from 'react-markdown'
import '../css/Projects.css'

const Projects = ({ name, children }) => (
    <Element name={name}>
        <div style={{ marginTop: "100px" }}>
            <Typography align='center' variant="h4" gutterBottom style={{ marginBottom: "20px" }}>Relevant Projects</Typography>
            {children}
        </div>
    </Element>
)

export default Projects

export class GithubProject extends Component {
    state = {
        expanded: false,
        markdown: null,
        title: ''
    }

    handleChange = () => {
        this.setState({
            expanded: !this.state.expanded
        });
    }

    componentDidMount() {
        fetch(this.props.githubReadme)
            .then(res => res.text())
            .then(text => {
                let highLevelDesc = text.split("#").slice(0, 2).join("#")
                let sep = highLevelDesc.split('\n')
                this.setState({ title: sep[0].split("#")[1] })
                let markdown = sep.slice(2, -1).join('\n')
                this.setState({ markdown })
            })
    }

    render() {
        return (
            <ExpansionPanel expanded={this.state.expanded} onChange={this.handleChange} style={{ marginLeft: "20px", marginRight: "20px" }}>
                <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
                    <Typography variant="h5" style={ styles.heading }>{this.state.title}</Typography>
                </ExpansionPanelSummary>
                <ExpansionPanelDetails style={{ flexDirection: "column" }}>
                    <ReactMarkdown source={this.state.markdown} escapeHtml={false} />
                </ExpansionPanelDetails>
            </ExpansionPanel>
        )
    }
}

export class Project extends Component {
    state = {
        expanded: false
    }

    handleChange = () => {
        this.setState({
            expanded: !this.state.expanded
        });
    }

    render() {
        return (
            <ExpansionPanel expanded={this.state.expanded} onChange={this.handleChange} style={{ marginLeft: "20px", marginRight: "20px" }}>
                <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
                    <Typography variant="h5" style={ styles.heading }>{this.props.title}</Typography>
                </ExpansionPanelSummary>
                {this.props.children}
            </ExpansionPanel>
        )
    }
}

export const ProjectDescription = ({ children }) => (
    <ExpansionPanelDetails style={{ flexDirection: "column" }}>
        <div>
            {children}
        </div>
    </ExpansionPanelDetails>
)

const styles = {
    heading: {
        // fontSize: 20,
        // flexBasis: '33.33%',
        // flexShrink: 0,
    },
    secondaryHeading: {
        fontSize: 30
    }
}