import React from 'react'
import linkedIn from '../img/linkedin.svg'
import Typography from '@material-ui/core/Typography'
import Grid from '@material-ui/core/Grid'
import Button from '@material-ui/core/Button'
import green from '@material-ui/core/colors/green'
import { Element } from 'react-scroll'

export const Header = ({ name, backgroundImg, children }) => (
    <Element name={name}>
        <div style={ styles.containerStyle }>
            <img style={ styles.backgroundImgStyle } src={backgroundImg} alt='background' />
            <div style={ styles.cardContainer }>
                {children}
            </div>
        </div>
    </Element>
)

export const MyImage = ({ me }) => (
    <Grid item xs={12}>
        <img style={ styles.meImgStyle } src={me} alt='me' />
    </Grid>
)

export const MyName = ({ children }) => (
    <Grid item xs={12}>
        <Typography style={ styles.textStyle } variant="h4">{children}</Typography>
    </Grid>
)

export const MyTitle = ({ children }) => (
    <Grid item xs={12}>
        <Typography style={ styles.textStyle } variant="h5">{children}</Typography>
    </Grid>
)

export const Group = ({ children }) => (
    <Grid container justify='center' style={{ marginTop: '50px' }} spacing={8}>
        {children}
    </Grid>
)

export const MyButton = ({ href, children }) => (
    <Grid item>
        <Button variant='contained' style={ styles.buttonStyle } href={href}>
            {children}
        </Button>
    </Grid>
)

export const LinkedInButton = ({ href }) => (
    <a href={href} target="_blank" rel="noopener noreferrer">
        <img src={linkedIn} style={{ position: "relative" }} alt='linkedIn' />
    </a>
)

const styles = {
    containerStyle: {
        width: '100%',
        position: 'relative',
        textAlign: 'center',
        overflow: 'hidden'
    },
    backgroundImgStyle: {
        position: 'relative',
        left: '100%',
        marginLeft: '-200%',
        width: '100%',
        minWidth: '1500px'
    },
    meImgStyle: {
        width: '200px',
        height: '200px',
        borderRadius: '50%',
        marginBottom: '50px'
    },
    cardContainer: {
        position: 'absolute',
        textAlign: 'center',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        width: '100%'
    },
    textStyle: {
        color: 'white',
        textAlign: 'center'
    },
    buttonStyle: {
        backgroundColor: green[500],
        color: 'white',
        width: '85px',
        height: '85px',
        margin: '10px'
    }
}