import React, { Component } from 'react'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import IconButton from '@material-ui/core/IconButton'
import MenuIcon from '@material-ui/icons/Menu'
import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'
import grey from '@material-ui/core/colors/grey'

import SwipeableDrawer from '@material-ui/core/SwipeableDrawer'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'

import withWidth from '@material-ui/core/withWidth'

import { Link } from 'react-scroll'

class GeneralNavigation extends Component {
    state = {
        drawerOpen: false
    }

    toggleDrawer = () => {
        this.setState({ drawerOpen: !this.state.drawerOpen })
    }

    render() {
        return (
            <div style={{ flexGrow: 1 }}>
                <AppBar id="webNavbar" position="fixed" style={{backgroundColor: grey[800]}}>
                    <Toolbar>
                        {this.props.width === 'xs' || this.props.width === 'sm' ? 
                            <IconButton onClick={this.toggleDrawer} color="inherit" aria-label="Menu" style={{marginRight: "1em"}}>
                                <MenuIcon />
                            </IconButton>
                        :
                            null
                        }

                        <Typography variant="title" color="inherit" style={{flexGrow: 1}}>
                            {this.props.title}
                        </Typography>

                        {this.props.width === 'xs' || this.props.width === 'sm' ? 
                            renderMobile(this.state.drawerOpen, this.toggleDrawer.bind(this), this.props.children)                       
                        :
                            renderDesktop(this.props.children)
                        }
                    </Toolbar>
                </AppBar>
            </div>
        )
    }
}

const renderMobile = (drawerOpen, toggleDrawer, children) => (
    <SwipeableDrawer open={drawerOpen} onClose={toggleDrawer} onOpen={toggleDrawer}>
        <div style={{width: '100%', maxWidth: 360}}>
            <List component="nav">
                {React.Children.map(children, child => {
                    return React.cloneElement(child, {
                        isMobile: true,
                        toggleDrawer
                    })
                })}
            </List>
        </div>
    </SwipeableDrawer> 
)

const renderDesktop = children => (
    React.Children.map(children, child => {
        return React.cloneElement(child, {
            isMobile: false
        })
    })
)

export const NavbarLink = ({ isMobile, to, children, toggleDrawer }) => {
    return (
        <Link to={to} spy={true} smooth={true} duration={500} offset={-70}>
        {isMobile ? 
            <ListItem button color="inherit" onClick={toggleDrawer}>
                <ListItemText primary={children} />
            </ListItem>
            :
            <Button color="inherit">{children}</Button>
        }
        </Link>
    )
}

export default withWidth()(GeneralNavigation)